//<МИКСИНЫ>===============================================================================================
@import "mixins.scss";

//<Подключаем шрифты>=======================================================================================
@import "fonts.scss";

//<Шрифт по умолчанию>==========================================================================================
$fontFamily: "Gotham Pro";
$fontFamilyAdd: "PT Sans", sans-serif;

//<ПЕРЕМЕННЫЕ>===========================================================================================================
$minWidth: 300px;
$maxWidth: 1920;
$maxWidthContainer: 1500;
$md1: $maxWidthContainer + 20;
$md2: 991.98;
$md3: 767.98;
$md4: 479.98;
$gold: #dfa94a;
$dark-gray: #262626;
//<ОБНУЛЕНИЕ, ОБЩИЕ ПАРАМЕТРЫ>===============================================================================================
@import "null";
body {
	font-size: 16px;
	line-height: 150%;
	background-color: #333;
	font-family: $fontFamily;
	&._lock {
		overflow: hidden;
	}
}

//<ОБОЛОЧКА>===========================================================================================================
.wrapper {
	width: 100%;
	min-height: 100%;
	overflow: hidden;
	display: flex;
	flex-direction: column;
	background: #f5f5f5;
	color: $dark-gray;
	@media (max-width: $md4 + px) {
		background-color: #fff;
	}
}

//<ОСНОВНАЯ СЕТКА>===========================================================================================================
.container {
	max-width: $maxWidthContainer + px;
	min-width: $minWidth;
	margin: 0 auto;
	background: #ffffff;
	padding: 0 26px;
	@media (min-width: $md1+px) {
		max-width: 1500px;
	}
	@media (max-width: $md2+px) {
		max-width: none;
	}
}
//</ОСНОВНАЯ СЕТКА>===========================================================================================================

//<ПОДКЛЮЧЕНИЕ ФАЙЛОВ СТИЛЕЙ СТРАНИЦ И ЭЛЕМЕНТОВ>====================================================================================================
@import "header";
@import "swiper/swiper-bundle.css";
@import "section1.scss";
@import "section2.scss";
@import "section3.scss";
@import "section4.scss";
@import "section5.scss";
@import "section6.scss";
@import "footer";

//</ПОДКЛЮЧЕНИЕ ФАЙЛОВ СТИЛЕЙ СТРАНИЦ И ЭЛЕМЕНТОВ>====================================================================================================

//<ОСНОВНОЙ БЛОК>====================================================================================================
.link_rect {
	border: 1px solid #7a7a7a;
	display: block;
	padding: 8px 20px 8px 30px;
	width: 152px;
	text-align: center;
	background: url("../img/icons/arrow-light.svg") 10px no-repeat;
	font-weight: bold;
	@media (max-width: $md3+px) {
		margin: 0 auto;
	}
}
.link_round {
	border: 1px solid #7a7a7a;
	border-radius: 50%;
	display: block;
	text-align: center;
	background: url("../img/icons/arrow-light.svg") 10px no-repeat;
}
.title {
	font-family: $fontFamily;
	font-size: 38px;
	font-weight: 700;
	line-height: 48px;
	letter-spacing: 0.03em;
	text-align: center;
	margin: 0 0 20px;
	&_s {
		font-size: 32px;
		line-height: 44px;
		position: relative;
		margin-bottom: 62px;
		padding-top: 48px;
		&::after {
			content: "";
			position: absolute;
			width: 7px;
			height: 7px;
			background-color: #a86d06;
			border-radius: 50%;
			bottom: -22px;
			left: calc(50% - 3.5px);
		}
	}
	@media screen and (max-width: $md2 + px) {
		font-size: 32px;
		line-height: 42px;
	}
	@media (max-width: $md3 + px) {
		&_s {
			font-size: 28px;
			line-height: 34px;
			position: relative;
			margin-bottom: 42px;
			padding-top: 38px;
		}
	}
	@media screen and (max-width: $md4 + px) {
		font-size: 28px;
		line-height: 34px;
	}
}
.dots {
	display: flex;
	justify-content: center;
	span {
		display: block;
		width: 7px;
		height: 7px;
		border-radius: 50%;
		background: $dark-gray;
		margin-right: 4px;
		&:last-of-type {
			margin-right: 0;
		}
	}
	@media screen and (max-width: $md4 + px) {
		span {
			margin-right: 9px;
			&:last-of-type {
				margin-right: 0;
			}
		}
	}
}
